#header {
  display: inline-block;
  transition: transform 0.5s ease-in-out;
}

#header:hover {
  transform: scale(1.1);
  text-shadow: 1px 1px 5px #0909094d;
}

h1#title {
  background: linear-gradient(0deg, #fff 0, #f6fbff);
  background-clip: text;
  -webkit-text-fill-color: #ffffff9e;
  text-transform: uppercase;
  -webkit-background-clip: text;
  margin: 0px 0px;
  font-size: xx-large;
}

h2 {
  margin: 0 3px;
  background-image: linear-gradient(0deg, #fff, #787878);
  background-clip: text;
  color: #ffffffbd;
  font-weight: 400;
  text-shadow: 2px 2px 5px #0000005c;
  -webkit-background-clip: text;
}

section {
  position: relative;
  margin: 1px;
}

.subtitle {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-size: small;
}

h2.subheading {
  position: relative;
}

h2.subheading:before,
h2.subheading:after {
  display: inline-block;
  position: relative;
  top: 1px;
  transition: all 0.5s ease-in-out;
  height: 100%;
  content: '[';
  color: #028635;
  font-size: 1.25em;
}

h2.subheading:before {
  color: #045e9f
}

h2.subheading:after {
  content: ']';
}

h2.subheading:hover:before {
  transform: translateX(-5px);
}

h2.subheading:hover:after {
  transform: translateX(5px);
}

h3 {
  background-image: linear-gradient(0deg, #fff, #787878);
  background-clip: text;
  color: #ffffffbd;
  -webkit-background-clip: text;
  margin: 8px;
  font-size: 1.4rem;
  text-shadow: 1px 1px 2px #0000002a;
}

h4 {
  margin: 0.3rem 0px 0.5rem 0px;
}

a {
  transition: border-color 0.3s ease, color 0.3s ease;
  border-bottom: 2px solid transparent;
  color: #007bff;
  text-decoration: none;
}

a:hover {
  border-bottom: 2px solid #0056b3;
  color: #0056b3;
}

img#about {
  margin: 10px 10px 0;
  box-shadow: 0px 0px 15px -3px #000;
  border: 1px solid #7fa1b9;
  border-radius: 50%;
  background-color: white;
  padding: 2px;
  width: 100px;
  height: 100px;
}

@media only screen and (max-width: 768px) {
  h1#title {
    position: relative;
    top: 2rem;
    float: left;
    margin-bottom: 10px;
    font-size: x-large;
  }

  img#about {
    float: left;
    margin-right: 10px;
    width: 64px;
    height: 64px;
  }

  #header {
    align-items: center;
    transform: translateX(-2vw);
  }

  section {
    position: relative;
    margin: 5px;
  }

  h2.subheading {
    font-size: small;
  }

  a:hover {
    border-bottom-width: 1px;
  }
}